export default {
	mounted() {
		// 类型重置
		uni.$on('typeReset',() =>{
			this.curretnTypeIndex = 0;
		})
	},
	data(){
		return {
			curretnTypeIndex:0,
			typeData:[],
		}
	},
	props:{
		itemData:Object
	},
	onReady() {
		// 获取类型数据
		this.get_type_data()
	},
	methods:{
		// 记账类型的切换
		typeClick(index){
			this.curretnTypeIndex = index;
			uni.$emit('typeClick',this.typeData[this.curretnTypeIndex].title,index)
		},
		get_type_data(){
			wx.cloud.callFunction({
			  name: 'get_data_type',
			  
			}).then(res => {
				this.typeData = res.result.data; 
			})
		}
	}
	
}
